.footer {
    display: flex;
    flex-direction: row;

    justify-content: space-between;

    max-width: 1280px;
    margin: 201px auto 39px;
}

.footer__logo {
    max-width: 238px;
    max-height: 51px;
}

.footer__nav-links {
    list-style-type: none;
    padding: 21px 0 0;

    display: flex;
    flex-direction: row;
    column-gap: 51px;
}

.footer__nav-link {
    transition: color .3s linear;

    padding: 0;
    margin: 0;

    color: #000;
    font-size: 21px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

.footer__nav-link:hover {
    cursor: pointer;
    color: #6d6d6d;
}

@media screen and (max-width: 1320px) {
    .footer {
        width: calc(100% - 40px);
        margin: 201px 20px 39px;
    }

    .contacts__container {
        width: calc(100% - 60px - 117px);
    }
}

@media screen and (max-width: 600px) {
    .footer__logo {
        max-width: 200px;
        max-height: 42px;
    }
}

@media screen and (max-width: 550px) {
    .footer__logo {
        max-width: 150px;
        max-height: 32px;

        padding-top: 8px;
    }

    .footer__nav-links {
        column-gap: 30px;
    }
}

@media screen and (max-width: 450px) {
    .footer__logo {
        max-width: 150px;
        max-height: 32px;

        padding-top: 8px;
    }

    .footer__nav-links {
        column-gap: 30px;
    }
}

@media screen and (max-width: 450px) {
    .footer {
        margin-top: 150px;
        display: flex;
        flex-direction: column;

        align-items: center;
    }

    .nav__link {
        font-size: 16px;
    }
}
